import { useState, useEffect, RefObject } from 'react';

export interface MousePosition {
  x: number | null;
  y: number | null;
  isOver: boolean;
}

const initialState = {
  x: null,
  y: null,
  isOver: false,
};

const useMousePointer = () => {
  const [element, setElement] = useState<HTMLElement | null>(null);
  const [mousePosition, setMousePosition] = useState<MousePosition>(
    initialState
  );

  const isPointerInElement = () => {
    console.log({ mousePosition });
    return true;
  };

  useEffect(() => {
    if (element !== null) {
      const onMove = (event: MouseEvent) => {
        const { clientX: x, clientY: y } = event;
        setMousePosition({ x, y });
      };

      const addEvent = element.addEventListener.bind(element);
      addEvent('mousemove', onMove);

      return () => {
        const removeEvent = element.addEventListener.bind(element);
        removeEvent('mousemove', onMove);
      };
    }
    return () => {};
  }, [element]);

  return {
    isPointerInElement,
    mousePosition,
    setRef: setElement,
  };
};

export default useMousePointer;
